<i18n>
{
  "en": {
    "RemoveBattery": "Remove Portable Battery",
    "ReturnBattery": "Return Portable Battery"
  },
  "zh": {
    "RemoveBattery": "租借充电宝",
    "ReturnBattery": "归还充电宝"
  }
}
</i18n>

<template>
  <div class="charger">
    <head-top proportion="15:70:15" :title='$t("RemoveBattery")' :go-back="true"></head-top>
    <div class="container">
      <div class="column">
        <div class="wrap">
          <img src="../../assets/scanning_images/lease.png" alt="">
          <button @click="goHandle(1)" v-if="!unReturndevice">{{ this.$t('RemoveBattery') }}</button>
          <button @click="goHandle(2)" v-else>{{ this.$t('ReturnBattery') }}</button>
        </div>
      </div>
      <!-- <div class="column">
        <div class="wrap">
          <img src="../../assets/scanning_images/return.png" alt="">
          <button @click="goHandle(2)">{{ this.$t('ReturnBattery') }}</button>
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
import {mapGetters} from 'vuex'
export default {
  name: 'charger',
  data () {
    return {}
  },

  components: {
    headTop
  },
  computed: {
    ...mapGetters(['unReturndevice'])
  },
  created () {
    if (this.ISDEBUG) {
      window.dataInteractionResponse('{"msgType":"0x5226","result":true,"data":{"type":"RETURN"}}')
    }
    this.dataInteractionResponse(data => {
      if (data.msgType === '0x5226' && data.result) {
        if (data.data.type === 'LENT') {
          this.$store.state.user.unReturndevice = false
        } else if (data.data.type === 'RETURN') {
          this.$store.state.user.unReturndevice = true
        }
      }
    })
  },
  mounted () {
    console.log(this.unReturndevice)
  },
  methods: {
    goHandle (num) {
      switch (num) {
        case 1:
          this.$router.push({path: '/charging'})
          break
        case 2:
          this.$router.push({path: '/restituteCharger'})
          break
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.charger{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    padding-top: 40%;
    .column{
      .wh(100%, 50%);
      position: relative;
      padding: 2% 5% 12% 5%;
      .wrap{
        .wh(100%, 100%);
        border-radius: 20px;
        box-shadow: 0px 4px 25px rgba(130,130,130,0.2); // 左上下右
        position: relative;
        img{
          height: 70%;
          margin-top: 5%;
        }
        button{
          .wh-l(480px, 80px);
          background: @themeColor;
          color: #585858;
          position: absolute;
          left: 50%;
          bottom: -40px;
          margin-left: -240px;
          border-radius: 40px;
          font-size: 30px;
        }
      }
    }
    .column:nth-child(2){
      padding: 0% 5% 12% 5%;
    }
  }
}
</style>



